const getFirmList = (data, callback) => {


	return function(dispatch) {
		dispatch({
			type: "TERMINALBRAND_FIRM",
			payload: data.firm
		})

		dispatch({
			type: "TERMINALBRAND_BRAND",
			payload: data.brand
		})

		var param = {
			firm: data.firm
		}

		//
		reqwest({
			url: '/common/firm.do',
			method: 'post',
			data: param,
			type: 'json',
			cache: true
		}).then((msg) => {



			if (msg.status) {

				dispatch({
					type: "TERMINALBRAND_FIRMLIST",
					payload: msg.data
				})

				callback(data);

			} else {
				notification['error']({
					description: msg.msg,
				});
			}

			if (msg.code == -1) {
				window.location.href = "/"
			}
		});
	}
}


